import Vue from 'vue'
import Router from 'vue-router'

import HomeView from "../components/home/HomeView.vue";
import rjwbView from "../components/rjwb/rjwbView.vue";
import rjwbView2 from "../components/rjwb/rjwbView2.vue";
import mshView from "../components/rjwb/mshView.vue";
import newsView from "../components/news/newsView.vue";
import newsdeView from "../components/news/newsdeView.vue";
import faqView from "../components/faq/faqView.vue";
import aboutView from "../components/header/aboutView.vue";

Vue.use(Router)

export default new Router({
//	mode: 'history',
  routes: [
 	{
		path:"/home",
		component:HomeView
	},
	{
		path:"/rjwb",
		component:rjwbView
	},
	{
		path:"/rjwb2",
		component:rjwbView2
	},
	{
		path:"/msh",
		component:mshView
	},
	{
		path:"/news",
		component:newsView,
		
	},

	{
		path:"/newsde",
		component:newsdeView
	},
	{
		path:"/faq",
		component:faqView
	},
	{
		path:"/about",
		component:aboutView
	},
	
	{
		path:"*",
		redirect:"/home"
	}
  	
  ],

  
})



